import ___CSS_LOADER_API_IMPORT___ from "../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, ".badge[data-v-7aa41ab6]:not(:last-child){margin-right:5px}", "",{"version":3,"sources":["webpack://./resources/js/components/User/Table.vue"],"names":[],"mappings":"AA+TA,yCACA,gBACA","sourcesContent":["<template>\n    <div>\n        <user-create :language-options=\"languageOptions\" :role-options=\"roleOptions\"></user-create>\n        <user-edit :user=\"user\" :language-options=\"languageOptions\" :role-options=\"roleOptions\" v-if=\"user\"></user-edit>\n\n        <h3>Users</h3>\n\n        <b-button\n            v-b-modal=\"'user-create'\"\n            variant=\"primary\"\n            class=\"my-4\"\n            v-b-tooltip.right=\"'Add new user'\"\n        >\n            <font-awesome-icon :icon=\"['fas', 'user-plus']\" size=\"lg\" />\n        </b-button>\n        \n        <b-table\n            striped\n            hover\n            :fields=\"fields\"\n            :items=\"items\"\n            primary-key=\"id\"\n            thead-class=\"text-center\"\n            stacked=\"lg\"\n            :per-page=\"perPage\"\n            :current-page=\"currentPage\"\n            :sort-by.sync=\"sortBy\"\n            :sort-desc.sync=\"sortDesc\"\n            :busy=\"isBusy\"\n        >\n            <template v-slot:cell(id)=\"data\">\n                {{ data.value }}\n            </template>\n\n            <template v-slot:cell(name)=\"data\">\n                {{ data.value }}\n            </template>\n\n            <template v-slot:cell(email)=\"data\">\n                <a :href=\"formatMailto(data.value)\">\n                    {{ data.value }}\n                </a>\n            </template>\n\n            <template v-slot:cell(email_verified_at)=\"data\">\n                <b-button\n                    pill\n                    variant=\"outline-success\"\n                    v-b-tooltip\n                    :title=\"formatDate(data.value)\"\n                    v-if=\"!!data.value\"\n                >\n                    <font-awesome-icon :icon=\"['fas', 'user-check']\" size=\"lg\" />\n                </b-button>\n                <b-button\n                    pill\n                    variant=\"outline-secondary\"\n                    disabled\n                    v-if=\"!data.value\"\n                >\n                    <font-awesome-icon :icon=\"['fas', 'user-check']\" size=\"lg\" />\n                </b-button>\n            </template>\n\n            \n            <template v-slot:cell(created_at)=\"data\">\n                {{ formatDate(data.value) }}\n            </template>\n\n            <template v-slot:cell(language)=\"data\">\n                {{ formatLanguage(data.value) }}\n            </template>\n\n            <template v-slot:cell(roles)=\"data\">\n                <b-badge v-for=\"role in data.value\" v-bind:key=\"role.id\" pill>{{ capitalize(role.name) }}</b-badge>\n            </template>\n\n            <template v-slot:cell(actions)=\"data\">\n                <b-button-group>\n                    <b-button\n                        variant=\"success\"\n                        @click=\"onEditUser(data.item)\"\n                        v-b-tooltip\n                        title=\"Edit user\"\n                    >\n                        <font-awesome-icon :icon=\"['fas', 'user-edit']\" size=\"lg\" />\n                    </b-button>\n                    <b-button\n                        variant=\"danger\"\n                        @click=\"onDeleteUser(data.item)\"\n                        v-b-tooltip\n                        title=\"Delete user\"\n                        :disabled=\"data.item.id === currentUser.id\"\n                    >\n                        <font-awesome-icon :icon=\"['fas', 'user-minus']\" size=\"lg\" />\n                    </b-button>\n                </b-button-group>\n            </template>\n        </b-table>\n\n        <b-pagination\n          v-model=\"currentPage\"\n          :total-rows=\"totalRows\"\n          :per-page=\"perPage\"\n          align=\"center\"\n          v-if=\"totalRows > perPage\"\n        ></b-pagination>\n    </div>\n</template>\n\n<script>\n    import { mapState } from 'vuex'\n    import UserCreate from './Create.vue'\n    import UserEdit from './Edit.vue'\n    import ToastHelpers from '../../mixins/ToastHelpers'\n    import { library } from '@fortawesome/fontawesome-svg-core'\n    import { faUserCheck, faUserEdit, faUserPlus, faUserMinus } from '@fortawesome/free-solid-svg-icons'\n\n    library.add(faUserCheck)\n    library.add(faUserEdit)\n    library.add(faUserPlus)\n    library.add(faUserMinus)\n\n    export default {\n        components: {\n            UserCreate,\n            UserEdit\n        },\n        mixins: [ToastHelpers],\n        computed: {\n            ...mapState({\n                perPage: state => state.app.itemsPerPage,\n                currentUser: state => state.app.user,\n                languages: state => state.app.languages\n            }),\n            totalRows() {\n                return this.items.length\n            },\n            fields() {\n                const fields = [\n                    {\n                        key: 'id',\n                        label: 'ID',\n                        sortable: true,\n                        tdClass: ['align-middle', 'text-center']\n                    },\n                    {\n                        key: 'name',\n                        sortable: false,\n                        tdClass: ['align-middle', 'text-center']\n                    },\n                    {\n                        key: 'email',\n                        sortable: false,\n                        tdClass: ['align-middle', 'text-center']\n                    },\n                    {\n                        key: 'email_verified_at',\n                        label: 'Verified',\n                        sortable: false,\n                        tdClass: ['align-middle', 'text-center']\n                    },\n                    {\n                        key: 'created_at',\n                        sortable: false,\n                        tdClass: ['align-middle', 'text-center']\n                    },\n                    {\n                        key: 'language',\n                        sortable: false,\n                        tdClass: ['align-middle', 'text-center']\n                    },\n                    {\n                        key: 'roles',\n                        sortable: false,\n                        tdClass: ['align-middle', 'text-center']\n                    },\n                    {\n                        key: 'actions',\n                        label: '',\n                        sortable: false,\n                        tdClass: ['align-middle', 'text-center']\n                    }\n                ]\n\n                return fields\n            },\n            roleOptions() {\n                return this.roles.map(role => {\n                    return {\n                        value: role.id,\n                        text: this.capitalize(role.name)\n                    }\n                })\n            },\n            languageOptions() {\n                return this.languages.map(language => {\n                    return {\n                        value: language.id,\n                        text: language.name\n                    }\n                })\n            }\n        },\n        data() {\n            return {\n                currentPage: 1,\n                sortBy: 'id',\n                sortDesc: true,\n                items: [],\n                isBusy: true,\n                user: null,\n                roles: []\n            }\n        },\n        methods: {\n            formatDate(dateString) {\n                const date = new Date(dateString)\n                \n                return `${date.getDate().toString().padStart(2, '0')}.${(date.getMonth() + 1).toString().padStart(2, '0')}.${date.getFullYear()} ${date.getHours().toString().padStart(2, '0')}:${date.getMinutes().toString().padStart(2, '0')}`\n            },\n            formatMailto(emailString) {\n                return `mailto:${emailString}`\n            },\n            onEditUser(user) {\n                this.user = user\n                this.$nextTick(() => {\n                    this.$bvModal.show('user-edit')\n                })\n            },\n            capitalize(string) {\n                return string.charAt(0).toUpperCase() + string.slice(1)\n            },\n            onDeleteUser(user) {\n                this.$bvModal.msgBoxConfirm(`Please confirm that you really want to delete user ${user.name}.`, {\n                    title: 'Please Confirm',\n                    size: 'sm',\n                    buttonSize: 'sm',\n                    okVariant: 'danger',\n                    okTitle: 'YES',\n                    cancelTitle: 'NO',\n                    footerClass: 'p-2',\n                    hideHeaderClose: false,\n                    centered: true\n                })\n                .then(value => {\n                    if (value) {\n                        this.isBusy = true\n                        axios.delete(`/users/${user.id}`)\n                        .then(response => {\n                            this.isBusy = false\n\n                            const index = this.items.findIndex(item => {\n                                return item.id === user.id\n                            })\n\n                            if (index !== -1) {\n                                this.items.splice(index, 1)\n                            }\n                        })\n                        .catch(error => {\n                            this.isBusy = false\n                            console.error('User delete:', error)\n\n                            this.displayHttpError(error)\n                        })\n                    }\n                })\n                .catch(err => {\n                    console.log('User delete confirmation error:', err)\n                })\n            },\n            formatLanguage(id) {\n                const language = this.languages.find(language => language.id === id)\n\n                return language ? language.name : id\n            }\n        },\n        created() {\n            axios.get('/users')\n            .then(response => {\n                this.isBusy = false\n                this.items = response.data.data\n            })\n            .catch(error => {\n                this.isBusy = false\n                console.error('Users loading:', error)\n            })\n\n            axios.get('/users/roles')\n            .then(response => {\n                this.isBusy = false\n                this.roles = response.data.data\n            })\n            .catch(error => {\n                this.isBusy = false\n                console.error('Roles loading:', error)\n            })\n\n            this.$root.$on('userCreated', user => {\n                this.items.push(user)\n            })\n\n            this.$root.$on('userUpdated', user => {\n                const index = this.items.findIndex(item => {\n                    return item.id === user.id\n                })\n                \n                if (index !== -1) {\n                    Vue.set(this.items, index, user)\n                } else {\n                    console.warn('Index not found, could not update a user:', user)\n                }\n            })\n        }\n    }\n</script>\n\n<style scoped>\n.badge:not(:last-child) {\n    margin-right: 5px;\n}\n</style>\n"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
